        :items="newtabs"
        :nextbtn="nextbtn"
        class="mx-auto"
        @click:next="next(valid)"
        @click:prev="prev()"
        @change="change()"
      >
        <v-tab-item class="pb-12" :eager="true">
          <form>
            <div class="text-center display-1 font-weight-light mb-6">
              导入原始数据
            </div>

            <v-row
              class="mx-auto"
