              <v-col cols="auto" class="text-center">
                <input
                  ref="file1"
                  type="file"
                  class="d-none"
                  @change="dataupload"
                />
                <v-card
                  :class="fileloaded ? 'success' : 'grey--text'"
                  class="mx-auto mt-0 d-inline-flex v-card--account"
                  outlined
                  height="106"
                  width="106"
                  @click="$refs.file1.click()"
                >
                  <v-icon class="mx-auto" size="96">{{ datastatus }}</v-icon>
                </v-card>

                <div class="font-weight-bold grey--text">选择导入数据文件</div>
              </v-col>
            </v-row>
          </form>
        </v-tab-item>
        <v-tab-item :eager="true">
          <v-row>
            <v-col cols="12" sm="12" class="px-10">
              <div
                id="dynamic_container"
                :style="{
                  width: '900px',
                  height: '450px',
                  position: 'relative'
                }"
              ></div>
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="10"
              sm="10"
              class="px-10"
              id="dynamic_result"
              :style="{
                width: '800px',
                height: '450px',
                position: 'relative',
                align: 'center'
              }"
            >
              <base-material-card
                :color="analyzed ? 'success' : 'grey'"
                dark
                icon="mdi-clipboard-plus"
                inline
