              >
                <v-data-table
                  :headers="tablehead"
                  :items="tableitems"
                  :loading="!analyzed"
                />
              </base-material-card>
            </v-col>
          </v-row>
        </v-tab-item>
        <v-tab-item class="pb-12" :eager="true">
          <form>
            <v-responsive class="mx-auto" max-width="500">
              <div
                class="text-center display-1 grey--text font-weight-light mb-6"
              >
                选取适用模型
              </div>

              <validation-provider rules="required" name="type">
                <input :value="modelselected" type="hidden" />
              </validation-provider>

              <v-item-group
                v-model="selectedmodel"
                class="row"
                @change="modelchange(selectedmodel)"
              >
                <v-item v-for="(account, i) in accounts" :key="i">
                  <template v-slot="{ active, toggle }">
                    <v-col class="text-center" cols="4">
                      <v-card
                        :class="active ? 'success' : 'grey'"
                        class="mb-6 mx-auto pa-10 d-inline-block v-card--account"
                        outlined
                        @click="toggle"
                      >
                        <v-icon large v-text="account.icon" />
                      </v-card>

                      <div
                        class="text-uppercase subtitle-2 text--primary"
                        v-text="account.type"
                      />
                    </v-col>
                  </template>
                </v-item>
              </v-item-group>
            </v-responsive>
          </form>
          <v-row>
            <v-col class="py-10 d-flex justify-center col-sm-12 col-lg-12">
              <div
                id="rawpie1_container"
                :style="{
                  width: '800px',
                  height: '480px',
                  position: 'relative'
                }"
              ></div>
            </v-col>
          </v-row>

          <v-row
            class="mx-auto"
            justify="space-around"
            style="max-width: 500px;"
          >
            <v-col cols="auto" class="text-center">
              <input
                ref="file2"
                type="file"
                class="d-none"
                @change="dataupload2"
              />
              <v-card
                :class="fileloaded2 ? 'success' : 'grey--text'"
                class="mx-auto mt-0 d-inline-flex v-card--account"
                outlined
                height="106"
                width="106"
                @click="$refs.file2.click()"
              >
                <v-icon class="mx-auto" size="96">{{ datastatus2 }}</v-icon>
              </v-card>

              <div class="font-weight-bold grey--text">导入预测数据</div>
            </v-col>
          </v-row>
        </v-tab-item>

        <v-tab-item :eager="true">
          <v-row justify="center" align="center" class="py-0 my-0">
            <v-col class="mb-2 py-4 px-4" cols="12" md="12" lg="12">
              <base-material-tabs color="success" icons-and-text vertical>
                <v-tab v-for="(tab, i) in showtabs" :key="i" class="mb-5 py-4">
                  {{ tab.text }}
                  <v-icon v-text="tab.icon" />
                </v-tab>
                <v-menu bottom left>
                  <template v-slot:activator="{ on }">
                    <v-btn
                      :disabled="more.length === 0"
                      text
                      class="align-self-center mr-4"
                      v-on="on"
                    >
                      更多示例
                      <v-icon right>mdi-menu-down</v-icon>
                    </v-btn>
                  </template>

                  <v-list class="ndblue3">
                    <v-list-item
                      v-for="item in more"
                      :key="item.text"
                      @click="subtab_click(item)"
                      >{{ item.text }}</v-list-item
                    >
                  </v-list>
                </v-menu>
                <v-menu bottom left>
                  <template v-slot:activator="{ on }">
                    <v-btn
                      :disabled="showtab_content.length === 0"
                      text
                      class="align-self-center mr-4"
                      v-on="on"
                    >
                      关闭示例
                      <v-icon right>mdi-menu-down</v-icon>
                    </v-btn>
                  </template>

                  <v-list class="ndblue3">
                    <v-list-item
                      v-for="item in showtab_content"
                      :key="item.text"
                      @click="closetab_click(item)"
                      >{{ item.text }}</v-list-item
                    >
                  </v-list>
                </v-menu>

                <v-tab-item
                  :eager="true"
                  v-for="tabcontent in showtabs"
                  :key="tabcontent.text"
                >
                  <!-- cols="[tabcontent.halfsize?'12':'12']" -->
                  <v-card flat class="my-2" :style="{ 'min-height': '240px' }">
                    <div
                      v-for="containerid in tabcontent.container"
                      :key="containerid"
                      cols="12"
                      sm="12"
                      :id="containerid"
                      :style="{
                        width: '800px',
                        height: '480px',
                        position: 'relative'
                      }"
                    ></div>
                  </v-card>
                </v-tab-item>
              </base-material-tabs>
            </v-col>
          </v-row>
        </v-tab-item>
      </base-show-wizard1>
    </validation-observer>
  </v-container>
</template>

<script>
import FAC from "@/common/calfapi";
import colors from "vuetify/lib/util/colors";
import theme from "@/echart_roma";
var rawpie2data = require("@/simudata/rawpie2.json");

export default {
  name: "DemoWizard",

  data: () => ({
    selectedmodel: 0,
    accounts: [
      {
        icon: "mdi-panda",
        type: "模型1"
      },
      {
        icon: "mdi-thermostat",
        type: "模型2"
      },
      {
        icon: "mdi-go-kart",
        type: "模型3"
      }
    ],
    tab: 0,
    fileloaded: false,
    fileloaded2: false,
    datastatus2: "mdi-database-plus",
    analyzed: false,
    datastatus: "mdi-database-plus",
    nextbtn: "导入",
    dyninterval: null,
    runcount: 0,
    more: [
      {
        text: "离网聚类分析",
        icon: "mdi-flash-outline",
        container: ["pie4_container"],
        halfsize: false
      }
    ],
    tablehead: [
      {
        text: "样本标签",
        value: "样本标签"
      },
      {
        text: "准确率",
        value: "准确率"
      },
      {
        text: "召回率",
        value: "召回率"
      },
      {
        text: "PR加权平均",
        value: "PR加权平均"
      },
      {
        text: "样本数量",
        value: "样本数量"
      }
    ],
    tableitems: [],
    tabs: [
      "导入数据",
      "初始数据",
      "模型孵化",
      "选用模型",
      "分析结果展示1",
      "分析结果展示2",
      "分析结果展示3"
    ],
    showtab_content: [
      {
        text: "分析示例1",
        icon: "mdi-database-import",
        container: ["bar1_container"],
        halfsize: false
      },
      {
        text: "分析示例2",
        icon: "mdi-database-import",
        container: ["pie1_container", "pie2_container"],
        halfsize: false
      },
      {
        text: "分析示例3",
        icon: "mdi-database-import",
        container: ["pie3_container"],
        halfsize: false
      }
    ],

    newtabs: [
      {
        text: "导入数据",
        icon: "mdi-database-import"
      },
      {
        text: "模型孵化",
        icon: "mdi-creation"
      },
      {
        text: "选用模型",
        icon: "mdi-gamepad-circle-down"
      },
      {
        text: "结果示例",
        icon: "mdi-golf"
      }
    ],

    rawpie_chart1: null,
    rawpie_opt1: null,
    rawpie_chart2: null,
    rawpie_opt2: null,
    rawpie_chart3: null,
    rawpie_opt3: null,

    dynamic_chart1: null,
    dynamic_opt1: null,

    bar_chart1: null,
    bar_opt1: null,

    pie_chart1: null,
    pie_opt1: null,
    pie_chart2: null,
    pie_opt2: null,
    pie_chart3: null,
    pie_opt3: null
  }),

  created() {
    FAC.log.info("demowizard created");
    this.$echarts.registerTheme("nokia", theme);
  },

  mounted() {
    FAC.log.info("echart1 mount");
  },

  computed: {
    showtabs() {
      console.log("showtabs updated!");
      return this.showtab_content;
    },

    modelselected() {
      return this.selectedmodel != null;
    },

    availableSteps() {
      const steps = [0];
      if (steps.includes(0) && this.fileloaded) steps.push(1);
      if (steps.includes(1) && this.analyzed) steps.push(2);
      if (this.modelselected && this.fileloaded2 && steps.includes(2))
        steps.push(3);
      /*
      if (steps.includes(4)) steps.push(5);
      if (steps.includes(5)) steps.push(6);
      if (steps.includes(6)) steps.push(7);
      */

      return steps;
    }
  },

  methods: {
    subtab_click(item) {
      console.log("add " + item.text + " clicked!");
      if (this.showtab_content.length <= 3) {
        this.showtab_content.splice(this.showtab_content.length, 0, item);

        for (var x in item.container) {
          let tmpid = item.container[x];
          setTimeout(() => {
            this.remounttab(tmpid);
          }, 1000);
        }

        var pos = this.more.indexOf(item);
        if (pos != -1) {
          this.more.splice(pos, 1);
        }
      }
    },

    closetab_click(item) {
      if (this.showtab_content.length >= 1) {
        var pos = this.showtab_content.indexOf(item);
        if (pos != -1) {
          this.showtab_content.splice(pos, 1);

          this.more.push(item);
        }
      }
    },

    analyze_done() {
      this.tableitems = [
        {
          样本标签: "在网",
          准确率: "99%",
          召回率: "100%",
          PR加权平均: "100%",
          样本数量: "1542631"
        },
        {
          样本标签: "流失",
          准确率: "93%",
          召回率: "96%",
          PR加权平均: "95%",
          样本数量: "38657"
        }
      ];

      this.analyzed = true;
    },

    dataupload() {
      this.fileloaded = true;
      this.datastatus = "mdi-database-check";
    },

    dataupload2() {
      this.fileloaded2 = true;
      this.datastatus2 = "mdi-database-check";
    },

    modelchange(event) {
      if (this.selectedmodel == 1) {
        this.rawpieswitch1();
      } else if (this.selectedmodel == 2) {
        this.rawpieswitch2();
      } else if (this.selectedmodel == 0) {
        this.rawpieswitch0();
      } else {
        console.log("model cancelled!" + this.selectedmodel);

        this.rawpie_chart1.clear();
      }
    },

    rawpieswitch0() {
      //console.log(this.rawpie_opt1.title.text);
      this.rawpie_opt1.series[0].data = rawpie2data.rawpie0.piedata;
      this.rawpie_chart1.setOption(this.rawpie_opt1, true);
    },

    rawpieswitch2() {
      this.rawpie_opt1.series[0].data = rawpie2data.rawpie2.piedata;
      this.rawpie_chart1.setOption(this.rawpie_opt1, true);
    },

    rawpieswitch1() {
      this.rawpie_opt1.series[0].data = rawpie2data.rawpie1.piedata;
      this.rawpie_chart1.setOption(this.rawpie_opt1, true);
    },

    getnextbtn() {
      if (this.tab == 0) {
        this.nextbtn = "导入";
      } else if (this.tab == 1) {
        this.nextbtn = "开始分析";
      } else {
        this.nextbtn = "下一页";
      }
    },

    next(valid) {
      if (this.tab === this.newtabs.length - 1) {
        console.log("Finished!");
      } else {
        this.tab++;
        this.getnextbtn();
        console.log("next to tab " + this.tab);
        this.mounttab();
      }
    },

    prev() {
      this.tab--;
      this.getnextbtn();
    },

    change() {
      console.log("tab changed to " + this.tab);
      this.mounttab();
    },

    remounttab(containerid) {
      console.log("remount tab start:" + containerid);
      if (containerid == "pie4_container") {
        this.rawpie2(containerid);
      } else if (containerid == "bar1_container") {
        this.barchart1(containerid);
      } else if (containerid == "pie1_container") {
        this.piechart1(containerid);
      } else if (containerid == "pie2_container") {
        this.piechart2(containerid);
      } else if (containerid == "pie3_container") {
        this.piechart3(containerid);
      }
    },

    mounttab() {
      if (this.tab === 1 && this.dynamic_chart1 === null) {
        console.log("mount dynamic");
        this.dynchart1("dynamic_container");
      } else if (this.tab === 2 && this.rawpie_chart1 === null) {
        console.log("mount show page2");
        this.rawpie1("rawpie1_container");
      } else if (this.tab === 3 && this.bar_chart1 === null) {
        console.log("mount show page3");
        this.barchart1("bar1_container");
        this.piechart1("pie1_container");
        this.piechart2("pie2_container");
        this.piechart3("pie3_container");
        //this.rawpie2("pie4_container");
      }

      /*
       else if (
        this.tab === 5 &&
        this.pie_chart1 === null &&
        this.pie_chart2 === null
      ) {
        console.log("mount show page2");
        this.piechart1("pie_chart1");
        this.piechart2("pie_chart2");
      } else if (this.tab === 6 && this.pie_chart3 === null) {
        console.log("mount show page3");
        this.piechart3("pie_chart3");
      }
      */
    },

    dynupdate1() {
      if (this.runcount >= 10) {
        console.log("runcount:" + this.runcount + " cleared!");

        clearInterval(this.dyninterval);
        this.analyze_done();
      } else {
        console.log("runcount:" + this.runcount);
        this.runcount++;
        app.count = 15;
        var axisData = new Date().toLocaleTimeString().replace(/^\D*/, "");

        var data0 = this.dynamic_opt1.series[0].data;
        var data1 = this.dynamic_opt1.series[1].data;
        data0.shift();
        data0.push(Math.round(Math.random() * 1000));
        data1.shift();
        data1.push((Math.random() * 10 + 5).toFixed(1) - 0);

        this.dynamic_opt1.xAxis[0].data.shift();
        this.dynamic_opt1.xAxis[0].data.push(axisData);
        this.dynamic_opt1.xAxis[1].data.shift();
        this.dynamic_opt1.xAxis[1].data.push(app.count++);

        this.dynamic_chart1.setOption(this.dynamic_opt1);
      }
    },

    dynchart1(containerid) {
      this.dynamic_chart1 = this.$echarts.init(
        document.getElementById(containerid),
        "nokia"
      );

      this.dynamic_opt1 = {
        backgroundColor: "#2c343c",
        grid: {
          x: 0, //左侧与y轴的距离
          y: 0, //top部与x轴的距离
          x2: 0, //右侧与y轴的距离
          y2: 0 //bottom部与x轴的距离
        },
        title: {
          text: "动态分析",
          subtext: "模拟运行中",
          textStyle: {
            color: "#ccc"
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#283b56"
            }
          }
        },
        legend: {
          data: ["最新趋势", "计算队列"],
          textStyle: {
            color: "#ccc"
          }
        },
        dataZoom: {
          show: false,
          start: 0,
          end: 100
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            data: (function() {
              var now = new Date();
              var res = [];
              var len = 15;
              while (len--) {
                res.unshift(now.toLocaleTimeString().replace(/^\D*/, ""));
                now = new Date(now - 2000);
              }
              return res;
            })()
          },
          {
            type: "category",
            boundaryGap: true,
            data: (function() {
              var res = [];
              var len = 15;
              while (len--) {
                res.push(15 - len - 1);
              }
              return res;
            })()
          }
        ],
        yAxis: [
          {
            type: "value",
            scale: true,
            name: "价格",
            max: 30,
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            type: "value",
            scale: true,
            name: "预购量",
            max: 1200,
            min: 0,
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: [
          {
            name: "计算队列",
            type: "bar",
            xAxisIndex: 1,
            yAxisIndex: 1,
            data: (function() {
              var res = [];
              var len = 15;
              while (len--) {
                res.push(Math.round(Math.random() * 100 + 7));
              }
              return res;
            })()
          },
          {
            name: "最新趋势",
            type: "line",
            itemStyle: {
              normal: {
                color: "#00FF00",
                lineStyle: {
                  color: "#00FF00"
                }
              }
            },
            data: (function() {
              var res = [];
              var len = 0;
              while (len < 10) {
                res.push((Math.random() * 10 + 5).toFixed(1) - 0);
                len++;
              }
              return res;
            })()
          }
        ]
      };
      this.dynamic_chart1.setOption(this.dynamic_opt1);

      var timesRun = 0;
      this.dyninterval = setInterval(this.dynupdate1, 500);
    },

    barchart1(containerid) {
      this.bar_chart1 = this.$echarts.init(
        document.getElementById(containerid),
        "nokia"
      );

      var dataMap = {};
      function dataFormatter(obj) {
        var pList = ["前四月", "前三月", "前二月", "前一月", "本月"];
        var temp;
        var times = ["9月", "11月"];
        for (var time in times) {
          temp = obj[times[time]];
          for (var i = 0, l = temp.length; i < l; i++) {
            obj[times[time]][i] = {
              name: pList[i],
              value: temp[i]
            };
          }
        }
        return obj;
      }

      dataMap.datacall1 = dataFormatter({
        "9月": [4265, 5761, 5844, 5981, 5982],
        "11月": [4125, 5261, 5344, 6381, 5777]
      });

      dataMap.datacall2 = dataFormatter({
        "9月": [899, 58, 18, 12, 10],
        "11月": [932, 66, 30, 22, 18]
      });

      dataMap.datacall3 = dataFormatter({
        "9月": [209, 17, 5, 4, 1],
        "11月": [219, 27, 15, 11, 3]
      });

      dataMap.datacall4 = dataFormatter({
        "9月": [161, 24, 3, 5, 3],
        "11月": [151, 32, 8, 12, 7]
      });

      dataMap.datacall5 = dataFormatter({
        "9月": [237, 40, 14, 1, 7],
        "11月": [142, 31, 12, 2, 1]
      });

      dataMap.datacall6 = dataFormatter({
        "9月": [110, 30, 12, 6, 6],
        "11月": [112, 32, 16, 4, 1]
      });

      dataMap.datacall7 = dataFormatter({
        "9月": [88, 12, 6, 3, 1],
        "11月": [67, 22, 16, 13, 5]
      });

      dataMap.datacall8 = dataFormatter({
        "9月": [47, 4, 3, 4, 4],
        "11月": [32, 3, 8, 2, 1]
      });

      dataMap.datacall9 = dataFormatter({
        "9月": [238, 152, 169, 49, 60],
        "11月": [228, 132, 149, 32, 50]
      });

      this.bar_opt1 = {
        baseOption: {
          timeline: {
            axisType: "category",
            // realtime: false,
            // loop: false,
            autoPlay: false,
            // currentIndex: 2,
            playInterval: 1000,
            // controlStyle: {
            //     position: 'left'
            // },
            data: ["9月", "11月"],
            label: {
              formatter: function(s) {
                return s;
              }
            }
          },
          title: {},
          tooltip: {},
          legend: {
            top: "6%",
            left: "right",
            data: [
              "主叫0-5分钟",
              "主叫5-30分钟",
              "主叫30-50分钟",
              "主叫50-80分钟",
              "主叫80-100分钟",
              "主叫100-150分钟",
              "主叫150-200分钟",
              "主叫200-250分钟",
              "主叫250分钟以上"
            ],
            selected: {
              "主叫0-5分钟": true,
              "主叫5-30分钟": true,
              主叫250分钟以上: true
            }
          },
          calculable: true,
          grid: {
            top: 80,
            bottom: 100,
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "shadow",
                label: {
                  show: true,
                  formatter: function(params) {
                    return params.value.replace("\n", "");
