import axiosflask from "./https";

var calfapi = {};

calfapi.get = function(inst, { url, config = {}, cb, cberr }) {
  axiosflask
    .get(url, config)
    .then(res => {
      if (res.status === 200) {
        let result = res.data;
        cb(inst, result);
      }
    })
    .catch(error => {
      cberr(inst, error);
    });
};

calfapi.post = function(inst, { url, data, cb, cberr }) {
  axiosflask
    .post(url, data)
    .then(res => {
      if (res.status === 201) {
        cb(inst, res.data);
      }
    })
    .catch(error => {
      cberr(inst, error);
    });
};

calfapi.postone = function(inst, { url, ident, cb, cberr }) {
  axiosflask
    .post(url + "/" + ident)
    .then(res => {
      if (res.status === 201) {
        cb(inst, res.data);
      }
    })
    .catch(error => {
      cberr(inst, error);
    });
};

calfapi.delete = function(inst, { url, delid, cb, cberr }) {
  axiosflask
    .delete(url + "/" + delid)
    .then(res => {
      if (res.status === 204) {
        cb(inst, res.data);
      }
    })
    .catch(error => {
      cberr(inst, error);
    });
};

